import fs from 'fs';
import behaviorify from './behaviorify';

/**
 * @function register
 * @param {function} require The reference to the global require
 * @param {object} [options] The optional behaviorify options
 */
export default function(require, options) {
    require.extensions['.bt'] = function(module, filename) {
        var content = fs.readFileSync(filename, 'utf8');
        var tree = JSON.parse(content);
        var compiled = behaviorify(tree, options);
        return module._compile(compiled, filename);
    };
}